<template>
  <div class="page-resume-education">
    <p class="resume-block-title">教育经历（从高中写起）</p>
    <el-form label-width="80px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in educationList" :key="index">
        <div class="item-block">
          <el-form-item label="起始年月">
            <el-date-picker
              v-model="item.startDate"
              type="date"
              placeholder="请输入出生年月"
              :picker-options="pickerOptions0">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="终止年月">
            <el-date-picker
              v-model="item.endDate"
              type="date"
              placeholder="请输入终止年月"
              :picker-options="pickerOptions0">
            </el-date-picker>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="学校名称">
            <el-input v-model="item.schoolName" placeholder="请输入学校名称"></el-input>
          </el-form-item>
          <el-form-item label="专业">
            <el-input v-model="item.majorName" placeholder="请输入专业名称"></el-input>
          </el-form-item>
        </div>

        <el-form-item label="学历">
          <el-select v-model="item.xl">
            <el-option v-for="(item, index) in educationOptions"
              :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
      </div>
      <div class="item-block item-add">
        <el-button @click="addList" icon="el-icon-plus">新增教育经历</el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left">上一步</el-button>
        <el-button type="primary">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        educationList: [{
          startDate: '',
          endDate: '',
          schoolName: '',
          majorName: '',
          xl: ''
        }],
        educationOptions: [{
          label: '高中',
          value: 0
        }, {
          label: '中专',
          value: 1
        }, {
          label: '大专',
          value: 2
        }, {
          label: '本科',
          value: 3
        }, {
          label: '硕士',
          value: 4
        }, {
          label: '博士',
          value: 5
        }]
      }
    },
    methods: {
      addList () {
        this.educationList.push({
          startDate: '',
          endDate: '',
          schoolName: '',
          majorName: '',
          xl: ''
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-education
    padding-bottom: 50px
</style>



