<template>
  <el-form class="user-form-card" label-width="80px">
    <div class="item-block">
      <el-form-item label="申请人">
        <el-input v-model="name" placeholder="申请人姓名"></el-input>
      </el-form-item>
      <el-form-item label="应聘岗位">
        <el-select v-model="position">
          <el-option v-for="(item, index) in positionOptions"
            :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="应聘区域">
        <el-select v-model="position">
          <el-option v-for="(item, index) in positionOptions"
            :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="form-item-date"
        label="简历投递日期" label-width="100px">
        <el-date-picker
          v-model="resumeDate"
          type="daterange"
          range-separator="-"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
      </el-form-item>
    </div>

    <div class="item-block">
      <el-form-item label="性别">
        <el-checkbox-group v-model="checkGenderList">
          <el-checkbox label="男"></el-checkbox>
          <el-checkbox label="女"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="婚姻状况">
        <el-checkbox-group v-model="checkMarriedList">
          <el-checkbox label="男"></el-checkbox>
          <el-checkbox label="女"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item class="form-item-date"
        label="出生日期" label-width="120px">
        <el-date-picker
          v-model="birthDate"
          type="daterange"
          range-separator="-"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
      </el-form-item>
    </div>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left">检索</el-button>
        <el-button>重置<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </el-form>
</template>

<script>
  export default {
    data () {
      return {
        name: '',
        position: '',
        resumeDate: '',
        birthDate: '',
        checkGenderList: ['男'],
        checkMarriedList: [],
        positionOptions: [{
          label: '数据工程师',
          value: 1
        }, {
          label: 'Java开发工程师',
          value: 2
        }]
      }
    }
  }
</script>

<style lang="stylus">
  .user-form-card
    border: 1px solid #ddd
    padding: 10px 15px
    margin-bottom: 35px
    .item-block
      display: flex
      &.btn-container
        justify-content: center
      .el-form-item
        flex: 1
    .el-input
      width: 125px
    .el-date-editor--daterange.el-input__inner
      width: 180px
</style>



