<template>
  <div class="page-resume-work">
    <p class="resume-block-title">工作经历（最近的5家）</p>
    <el-form label-width="85px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in workList" :key="index">
        <div class="item-block">
          <el-form-item label="起始时间">
            <el-date-picker
              v-model="item.startDate"
              type="date"
              placeholder="请输入起始时间"
              :picker-options="pickerOptions0">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="终止时间">
            <el-date-picker
              v-model="item.endDate"
              type="date"
              placeholder="请输入终止时间"
              >
            </el-date-picker>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="工作单位">
            <el-input v-model="item.workPlace" placeholder="请输入工作单位"></el-input>
          </el-form-item>
          <el-form-item  label="职位">
            <el-input v-model="item.position" placeholder="请输入职位"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="薪资">
            <el-input v-model="item.salary" placeholder="请输入薪资"></el-input>
          </el-form-item>
          <el-form-item label="离职原因">
            <el-input v-model="item.leaveReason" placeholder="请输入离职原因"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="证明人">
            <el-input v-model="item.proveName" placeholder="请输入证明人"></el-input>
          </el-form-item>
          <el-form-item label="证明人电话">
            <el-input v-model="item.proveTel" placeholder="请输入证明人电话"></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addList" icon="el-icon-plus">新增工作经历</el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left">上一步</el-button>
        <el-button type="primary">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        workList: [{
          startDate: '',
          endDate: '',
          workPlace: '',
          position: '',
          salary: '',
          leaveReason: '',
          proveName: '',
          proveTel: ''
        }],
        workOptions: [{
          label: '高中',
          value: 0
        }, {
          label: '中专',
          value: 1
        }, {
          label: '大专',
          value: 2
        }, {
          label: '本科',
          value: 3
        }, {
          label: '硕士',
          value: 4
        }, {
          label: '博士',
          value: 5
        }]
      }
    },
    methods: {
      addList () {
        this.workList.push({
          startDate: '',
          endDate: '',
          workPlace: '',
          position: '',
          salary: '',
          leaveReason: '',
          proveName: '',
          proveTel: ''
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-work
    padding-bottom: 50px
</style>



