<template>
  <div class="page-resume-family">
    <p class="resume-block-title">家庭成员（已婚者须填写配偶、子女情况；未婚者填写父母及兄弟姐妹）</p>
    <el-form label-width="80px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in familyMembers"
        :key="index">
        <div class="item-block">
          <el-form-item label="姓名">
            <el-input v-model="item.name" placeholder="请输入成员姓名"></el-input>
          </el-form-item>
          <el-form-item label="关系">
            <el-input v-model="item.relation" placeholder="请输入关系"></el-input>
          </el-form-item>
        </div>

         <div class="item-block">
          <el-form-item label="年龄">
            <el-input v-model="item.age" placeholder="请输入成员年龄"></el-input>
          </el-form-item>
          <el-form-item label="居住地">
            <el-input v-model="item.address" placeholder="请输入成员居住地"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="工作单位">
            <el-input v-model="item.workPlace" placeholder="请输入成员工作单位"></el-input>
          </el-form-item>
          <el-form-item label="职务">
            <el-input v-model="item.position" placeholder="请输入成员职务"></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addMembers" icon="el-icon-plus">新增家庭成员</el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left">上一步</el-button>
        <el-button type="primary">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        familyMembers: [{
          name: '',
          relation: '',
          age: '',
          address: '',
          workPlace: '',
          position: ''
        }]
      }
    },
    methods: {
      addMembers () {
        this.familyMembers.push({
          name: '',
          relation: '',
          age: '',
          address: '',
          workPlace: '',
          position: ''
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-family
    padding-bottom: 50px
</style>



