<template>
  <div class="content-view">
    <v-header></v-header>
    <div class="content-menu">
      <router-link v-for="(item, index) in routerMenus"
        :to="{name: item.name}" :key="index">
        <el-button>{{item.label}}</el-button>
      </router-link>
    </div>
    <div class="user-content-route">
      <router-view/>
    </div>
  </div>
</template>

<script>
  import VHeader from './header'

  export default {
    data () {
      return {
        routerMenus: [
          { name: 'resume', label: '简历管理' },
          { name: 'interview', label: '面试管理' },
          { name: 'talnent', label: '人才管理' },
          { name: 'not-satisfied', label: '不合格库' }
        ]
      }
    },
    components: { VHeader }
  }
</script>

<style lang="stylus">
  .content-menu
    position: fixed
    top: 100px
    width: 150px
    height: 100%
    padding: 0 25px
    .el-button
      margin-bottom: 35px
  .user-content-route
    padding-left: 200px
    padding-top: 50px
    padding-right: 50px

  .router-link-active .el-button
    color: #09AD8A
    border-color: rgb(181, 230, 220)
    background-color: rgb(230, 247, 243)
</style>



