<template>
  <div class="page-resume-baseinfo">
    <el-form label-width="80px" label-position="left">
      <div class="item-block">
        <el-form-item label="申请职位">
          <el-select v-model="value" placeholder="请选择申请职位">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="应聘区域">
          <el-select v-model="value" placeholder="请选择申请职位">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="可到职日">
          <el-date-picker
            v-model="workVal"
            type="date"
            placeholder="选择日期"
            :picker-options="pickerOptions0">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="期望薪资">
          <el-input placeholder="请输入期望薪资"></el-input>
        </el-form-item>
      </div>

      <el-form-item label="招聘渠道">
        <el-checkbox-group v-model="checkList">
          <el-checkbox label="网络招聘"></el-checkbox>
          <el-checkbox label="人才市场"></el-checkbox>
          <el-checkbox label="中介公司"></el-checkbox>
          <el-checkbox label="内部引荐"></el-checkbox>
          <el-checkbox label="其他"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
    </el-form>

    <div class="baseinfo-block">
      <p class="resume-block-title">基本信息</p>
      <el-form label-width="80px" label-position="left">
        <div class="item-block">
          <el-form-item label="姓名">
            <el-input placeholder="请输入姓名"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-select v-model="genderVal">
              <el-option v-for="(item, index) in genderOptions"
                :key="index" :label="item.label"
                :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="出生年月">
            <el-date-picker
              v-model="birthVal"
              type="date"
              placeholder="请输入出生年月"
              :picker-options="pickerOptions0">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="身份证号">
           <el-input placeholder="请输入身份证号"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="身高">
           <el-input placeholder="请输入身高"></el-input>
          </el-form-item>
          <el-form-item label="体重">
           <el-input placeholder="请输入体重"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="籍贯">
           <el-input placeholder="请输入籍贯"></el-input>
          </el-form-item>
          <el-form-item label="政治面貌">
            <el-select v-model="politicsVal">
              <el-option v-for="(item, index) in politicsOptions"
                :key="index" :label="item.label"
                :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="婚姻状况">
            <el-checkbox-group v-model="checkMarriedList">
              <el-checkbox label="未婚"></el-checkbox>
              <el-checkbox label="已婚"></el-checkbox>
              <el-checkbox label="已婚已育"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="是否有服兵役" label-width="120px">
            <el-checkbox-group v-model="checkMilitaryList">
              <el-checkbox label="是"></el-checkbox>
              <el-checkbox label="否"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </div>
      </el-form>
      <div class="item-block btn-container">
        <el-button type="primary">下一步</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import VHeader from './header'

  export default {
    data () {
      return {
        options: [{
          value: '1',
          label: '黄金糕'
        }, {
          value: '2',
          label: '双皮奶'
        }, {
          value: '3',
          label: '蚵仔煎'
        }, {
          value: '4',
          label: '龙须面'
        }, {
          value: '5',
          label: '北京烤鸭'
        }],
        value: '',
        pickerOptions0: {
          disabledDate (time) {
            return time.getTime() > Date.now()
          }
        },
        birthVal: '',
        checkList: ['网络招聘', '人才市场'],
        genderOptions: [{
          label: '男',
          value: 0
        }, {
          label: '女',
          value: 1
        }],
        genderVal: '',
        workVal: '',
        politicsOptions: [{
          label: '团员',
          value: 1
        }, {
          label: '党员',
          value: 2
        }, {
          label: '群众',
          value: 3
        }],
        politicsVal: '',
        checkMarriedList: ['未婚'],
        checkMilitaryList: ['是']
      }
    },
    components: { VHeader }
  }
</script>

<style lang="stylus">
.page-resume-baseinfo
  padding-bottom: 50px
</style>



