<template>
  <div class="page-resume-position">
    <p class="resume-block-title">职业信息专栏（仅限营销司机应聘人员填写）</p>
    <el-form label-width="120px" label-position="left">
      <div class="item-block">
        <el-form-item label="驾照类型" label-width="80px">
          <el-select v-model="value" placeholder="请选择驾照类型">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="领证地点" label-width="80px">
         <el-input placeholder="请输入领证地点"></el-input> </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="有效日期" label-width="80px">
          <el-date-picker
            v-model="jzExpiresVal"
            type="date"
            placeholder="请选择可到职日期"
            :picker-options="pickerOptions0">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="实际驾龄" label-width="80px">
         <el-input placeholder="请输入实际驾龄"></el-input> </el-form-item>
      </div>

      <el-form-item label="驾驶过的车辆类型" label-width="160px">
        <el-checkbox-group v-model="checkCarCategories">
            <el-checkbox label="1.5吨以上货车"></el-checkbox>
            <el-checkbox label="其他"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="有无货运上岗证" label-width="160px">
        <el-checkbox-group v-model="checkList">
            <el-checkbox label="有"></el-checkbox>
            <el-checkbox label="无"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="货运上岗证有效日期至" label-width="160px">
        <el-date-picker
          v-model="sgExpiresVal"
          type="date"
          placeholder="请输入有效日期至"
          :picker-options="pickerOptions0">
        </el-date-picker>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left">上一步</el-button>
        <el-button type="primary">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        pickerOptions0: {
          disabledDate (time) {
            return time.getTime() > Date.now()
          }
        },
        value: '',
        jzExpiresVal: '',
        sgExpiresVal: '',
        checkCarCategories: ['1.5吨以上货车'],
        checkList: ['有'],
        options: [{
          value: 'A1',
          label: 'A1'
        }, {
          value: 'A2',
          label: 'A2'
        }, {
          value: 'A3',
          label: 'A3'
        }, {
          value: 'B1',
          label: 'B1'
        }, {
          value: 'B2',
          label: 'B2'
        }, {
          value: 'C1',
          label: 'C1'
        }, {
          value: 'C2',
          label: 'C2'
        }, {
          value: 'C3',
          label: 'C3'
        }, {
          value: 'C4',
          label: 'C4'
        }, {
          value: 'D',
          label: 'D'
        }, {
          value: 'E',
          label: 'E'
        }, {
          value: 'F',
          label: 'F'
        }, {
          value: 'M',
          label: 'M'
        }, {
          value: 'N',
          label: 'N'
        }, {
          value: 'P',
          label: 'P'
        }],
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-position
    padding-bottom: 50px
</style>



