<template>
  <div class="page-resume-shengao">
    <p class="resume-block-title">申告事项</p>
    <el-form>
      <el-form-item label="您是否在两年内有发生过交通违章？若有，请详细列出">
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"></el-input>
      </el-form-item>

      <el-form-item label="您是否因违法行为而被拘捕、民事或刑事诉讼过？若有，请详细列出">
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"></el-input>
      </el-form-item>

      <el-form-item label="您是否被公司解除劳动合同或和公司协商解除劳动合同过？若有，请详细列出">
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入证明人"></el-input>
      </el-form-item>

      <el-form-item label="您是否有亲属朋友在雅玛多关联公司工作？若有，请列出姓名，部门名称及职位">
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入证明人"></el-input>
      </el-form-item>

      <el-form-item label="您是否同意公司对您进行工作地点的调动?">
        <el-checkbox-group v-model="checkMarriedList">
          <el-checkbox label="同意"></el-checkbox>
          <el-checkbox label="不同意"></el-checkbox>
        </el-checkbox-group>
        <el-input :autosize="textareaSize" type="textarea" placeholder="可补充说明"></el-input>
      </el-form-item>

      <el-form-item label="您是否同意接受公司为您安排的其它岗位？?">
        <el-checkbox-group v-model="checkMarriedList">
          <el-checkbox label="同意"></el-checkbox>
          <el-checkbox label="不同意"></el-checkbox>
        </el-checkbox-group>
        <el-input :autosize="textareaSize" type="textarea" placeholder="可补充说明"></el-input>
      </el-form-item>

      <el-form-item label="您是否同意接受不超过一年的外派工作?">
        <el-checkbox-group v-model="checkMarriedList">
          <el-checkbox label="同意"></el-checkbox>
          <el-checkbox label="不同意"></el-checkbox>
        </el-checkbox-group>
        <el-input :autosize="textareaSize" type="textarea" placeholder="可补充说明"></el-input>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
      <el-button type="primary">提交简历</el-button>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        textareaSize: { minRows: 5, maxRows: 8 },
        shengaoOptions: [{
          label: '高中',
          value: 0
        }, {
          label: '中专',
          value: 1
        }, {
          label: '大专',
          value: 2
        }, {
          label: '本科',
          value: 3
        }, {
          label: '硕士',
          value: 4
        }, {
          label: '博士',
          value: 5
        }]
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-shengao
    padding-bottom: 50px
</style>



