<template>
  <div class="page-resume-technology">
    <p class="resume-block-title">技能及个人特长</p>
    <el-form>
      <el-form-item label="您会使用何种电脑软件?（例如：EXCEL,WORD,PPT等）">
        <el-checkbox-group v-model="checkList">
          <el-checkbox label="EXCEL"></el-checkbox>
          <el-checkbox label="WORD"></el-checkbox>
          <el-checkbox label="PPT"></el-checkbox>
        </el-checkbox-group>
        <el-input type="textarea" placeholder="如有其他请填写"></el-input>
      </el-form-item>

      <el-form-item label="曾经参加过何种专业技能培训 ?">
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"></el-input>
      </el-form-item>

      <el-form-item label="外语水平 / 其他技能">
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"></el-input>
      </el-form-item>

      <el-form-item label="个人兴趣爱好、特长">
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入证明人"></el-input>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left">上一步</el-button>
        <el-button type="primary">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        checkList: [],
        textareaSize: { minRows: 5, maxRows: 8 },
        technologyOptions: [{
          label: '高中',
          value: 0
        }, {
          label: '中专',
          value: 1
        }, {
          label: '大专',
          value: 2
        }, {
          label: '本科',
          value: 3
        }, {
          label: '硕士',
          value: 4
        }, {
          label: '博士',
          value: 5
        }]
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-technology
    padding-bottom: 50px
</style>



